<template>
  <div class="header">
    <ul class="menu">
      <li v-for="(item,index) in list" :key="item.title" >
        <router-link :class="{'title-selected':titleSelect==index}" :to="item.path" @click.native="tabClass(index)" class="title">{{item.title}}</router-link>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "Header",
  data() {
    return {
      list:[
        {
          title:'HOME',
          path:'/index'
        },{
          title:'ABOUT',
          path:'/about'
        },{
          title:'NOTE',
          path:'/note'
        }
      ],
      titleSelect:0
    };
  },
  components: {},
  methods:{
    tabClass(index){
      this.titleSelect = index;
    }
  }
};
</script>

<style scoped>
.header {
  font-family:'Courier New', Courier, monospace;
  padding: 0;
  width: 20vw;
  height: 99vh;
  position: fixed;
  left: 0;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #000;
}
.menu {
  list-style-type: none;
  margin-right: 30px;
}
li {
  padding: 5px;
}
.title{
  cursor:pointer;
  font-size:1.2em;
  color: 	#C0C0C0;
  transition: 0.25s;
  text-decoration:none;
}
.title:hover{
  color: white;

}
.title-selected{
  color: white;
}
</style>



