import Vue from 'vue'
import Router from 'vue-router'
import Header from '@/components/Header'
import Home from '@/components/Home'
import Note from '@/components/Note'
import About from '@/components/About'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect:'/index'
    },
    {
      path: '/index',
      name: 'index',
      components: {
        header:Header,
        home:Home
      } 
    },
    {
      path: '/note',
      name: 'note',
      components: {
        header:Header,
        home:Note
      } 
    },
    {
      path: '/about',
      name: 'about',
      components: {
        header:Header,
        home:About
      } 
    }
  ]
})



